import React, { useContext } from 'react';
import { ThemeContext } from 'providers/ThemeProvider';
import { Header } from 'components/theme';
import { Container, Button } from 'components/common';
import { Wrapper, IntroWrapper, Details, Thumbnail } from './styles';

import { useStaticQuery ,graphql } from "gatsby"
import Img from "gatsby-image"

import HeadShot from 'assets/images/pamheadshot_tall-oval_opt.png'

export const Intro = (  ) => {
  const { theme } = useContext(ThemeContext);


  const data = useStaticQuery(graphql`
  query {
    image: file(relativePath: { eq: "pamheadshot_tall-oval_opt.png" }) {
      childImageSharp {
        fixed(width: 340, quality: 85) {
          ...GatsbyImageSharpFixed
        }
      }
    }
  }
  `)


  return (
    <Wrapper >
      <Header />
      <IntroWrapper as={Container}>
        <Details theme={theme}>
          <h1>Pamela Welch</h1>
          <h4>A proven professional with extensive experience in all facets of communications and marketing.</h4>
          <p>Seeking full-time, project, and consultation work.</p> 
          <a href='mailto:pamwelch98@gmail.com'>
            <Button as='button'  >
              Contact Me
            </Button>
          </a>
        </Details>
        <Thumbnail>

          {/* <img src={HeadShot} alt="I’m Pam and I’m an experienced Marketer" /> */}
        <Img fixed={data.image.childImageSharp.fixed}></Img>

        </Thumbnail>
      </IntroWrapper>
    </Wrapper>
  );
};
