import React, { useContext } from 'react';
import { ThemeContext } from 'providers/ThemeProvider';
import { Container, Card } from 'components/common';
import { Link, withPrefix } from 'gatsby'
import { Wrapper, FlexList, Item, Content, Stats, Flex, Thumbnail, Details } from './styles';

// images
import FoundationsNewsletter from 'assets/images/SunnyviewFoundationNewsletter.png';
import WOH from 'assets/images/TroyHospitalstimelinemural_croped.png'
import RecruitmentBook from 'assets/images/PhysicianRecruitmentBooklet.png'

export const Projects = () => {
    const { theme } = useContext(ThemeContext)


    const imgSty = { objectFit: "contain" };

    return (<>

        <Wrapper as={Container} id="projects">
            <h1>Projects</h1>
            <FlexList>
                {/* Troy Master Facility Project */}
                <Card theme={theme}>
                    <Content>
                        <Details>

                            <h3>Healthy Future. Health Troy.</h3>
                            <p>First announced in 2012 by St. Peter’s Health Partners, the Troy Master Facility Plan was a $99.9 million, multi-phased plan to transform healthcare in Rensselaer County and its surrounding communities.</p>
                            <p>The project included construction, renovation, and modernization efforts aimed at improving inpatient facilities on the Samaritan campus and outpatient facilities on the St. Mary’s campus.</p>
                            <p>As Communications and Marketing Manager, I led the marketing and communications efforts throughout all phases of the project, including plan development and implementation.</p>
                            <p>Working with a local creative firm, an overarching campaign theme, <i>Healthy Future. Healthy Troy.</i>, was developed. This theme and consistent visual concept was woven into all campaign communications and marketing materials throughout the six-year project.</p>

                            <p>Major elements of the campaign included:</p>
                            <ul>
                                <li>A unique website landing page and web pages providing project updates, construction alerts, and milestones</li>
                                <li><a href={withPrefix("/Website_Samples/TroyMasterFacilityProject/MFPcolleague.pdf")} target="_enewsletter" >Colleague e-newsletter</a></li>
                                <li><a href={withPrefix("/Website_Samples/TroyMasterFacilityProject/MFPprintad.pdf")} target="_printadvert">Print advertising</a></li>
                                <li>Digital ads</li>
                                <li>Social media</li>
                                <li>Outdoor (Out of Home)</li>
                                <li><a href={withPrefix("/Website_Samples/TroyMasterFacilityProject/MFPPullUp.pdf")} target="_pullupdisp">Pull-up displays</a></li>
                                <li><a href={withPrefix("/Website_Samples/TroyMasterFacilityProject/MFPMergerBrochure.pdf")} target="_collateralmat">Collateral materials</a></li>
                                <li>Direct mail</li>
                                <li>Television ads</li>
                                <li>Signage</li>
                                <li>Earned media</li>
                            </ul>

                        </Details>
                        <Thumbnail>

                            <a href={withPrefix("/Website_Samples/TroyMasterFacilityProject/MFP-Brochure.pdf")} target="_brochure">
                                <img style={imgSty, { border: "solid black" }} src="image/HealthyFutureHealthyTroyFull.png" alt="Drop in image" />
                            </a>

                        </Thumbnail>
                    </Content>
                    <hr />
                </Card>

                {/* Alzheimer's Caregiver Support Initiative */}
                <Card theme={theme}>

                    <Content>

                        <Details>
                            <h3>Alzheimer's Caregiver Support Initiative</h3>
                            <p>Working with Eddy Alzheimer’s Services, I led the marketing and communications efforts for The Alzheimer's Caregiver Support Initiative. The program is a New York State grant funded effort which seeks to alleviate the financial and emotional burden placed on caregivers.</p>
                            <p>Major elements of the campaign included:</p>
                            <ul>
                                <li>A unique website landing page and web pages</li>
                                <li>Video
                                    <ul>
                                        <li><a href="https://youtu.be/jjh8lyNBq6w" target="_overview">Program overview video</a></li>
                                        <li><a href="https://youtu.be/gumg9D2v_KY" target="_pattiallen">Caregiver videos – Patti Allen</a></li>
                                        <li><a href="https://youtu.be/sYreiQ3CWvk" target="_patsager">Caregiver video – Pat Sager</a></li>
                                    </ul>
                                </li>
                                <li>Print advertising</li>
                                <li>Digital ads</li>
                                <li>Social media</li>
                                <li><a href={withPrefix("/Website_Samples/Alzheimer_sCaregiverSupportInitiative/AlzOutdoor.pdf")} target="_outdoor">Outdoor (Out of Home)</a></li>
                                <li><a href={withPrefix("/Website_Samples/Alzheimer_sCaregiverSupportInitiative/EAS-AlzCaregiverSupport_brochure.pdf")} target="_collateralmat">Collateral materials</a></li>
                                <li>Earned media</li>
                            </ul>

                        </Details>
                        <Thumbnail>

                            <iframe width="560" height="315" src="https://www.youtube.com/embed/jjh8lyNBq6w" frameborder="0" allow="  clipboard-write; encrypted-media; picture-in-picture" allowfullscreen></iframe>

                        </Thumbnail>
                    </Content>
                    <hr />
                </Card>

                {/* Sexual Assualt Bystander Campaign */}
                <Card theme={theme}>
                    <Content>
                        <Details>
                            <h3>Take a Stand Troy</h3>
                            <p>The primary target audience for the campaign was adults age 18-34 in Troy, NY. The grant-funded campaign encouraged individuals witnessing harassment or abuse to step forward and take action rather than being a bystander. This initiative is coordinated through the Sexual Assault and Crime Victims Assistance Program.</p>
                            <p>The creative for the campaign required compelling photography and featured well-known individuals in the Troy community. The campaign elements included:</p>
                            <ul>
                                <li>Website</li>
                                <li>Social media</li>
                                <li>Press release/ Press event</li>
                                <li><a href={withPrefix("/Website_Samples/SexualAssultBystanderCampaign/SexualAssualtposter.pdf")} target="_poster">Poster series</a></li>
                                <li><a href={withPrefix("/Website_Samples/SexualAssultBystanderCampaign/SexualAssualtPanelCard.pdf")} target="_panel">Panel cards</a></li>
                                <li><a href={withPrefix("/Website_Samples/SexualAssultBystanderCampaign/SexualAssualtwindow.pdf")} target="_cling">Car window clings</a></li>
                            </ul>
                        </Details>
                        <Thumbnail style={{ alignItems: "center" }}>

                            <a href={withPrefix("/Website_Samples/SexualAssultBystanderCampaign/SexualAssualtPanelCard.pdf")} target="_pannel">
                                <img style={{ border: "black solid", width: "300px", margin: "auto" }} src="image/TroyTakeAStand.png" alt="Drop in image" />
                            </a>

                        </Thumbnail>
                    </Content>
                    <hr />
                </Card>

                {/* Eddy Retirement Living TV and Videos */}
                <Card theme={theme}>
                    <Content>
                        <Details>
                            <h3>Eddy Retirement Living TV and Videos</h3>
                            <p>Throughout my career I’ve had the good fortune to coordinate, concept, and oversee the production of many television campaigns. I collaborated with a small in-house creative team to concept and create this :30 television commercial for Eddy Senior Living. The spot was shot using a local production company.</p>

                            <ul>
                                <li><a href="https://youtu.be/TfSt1CiDpd8" target="_overview">Eddy Senior Living</a></li>
                            </ul>

                        </Details>
                        <Thumbnail>

                            <iframe width="560" height="315" src="https://www.youtube.com/embed/TfSt1CiDpd8" frameborder="0" allow="accelerometer; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>

                        </Thumbnail>

                    </Content>
                    <hr />
                </Card>

                {/* Foundation Newsletters */}
                <Card theme={theme}>
                    <Content>
                        <Details>

                            <h3>Foundation Newsletters</h3>
                            <p>I have extensive experience working with foundations to develop communications strategies and messaging. A few of the many projects include, collaborating on donor events, invitations and programs, remarks, brochures and other collateral pieces, press events, and newsletters. I led the editorial and graphics team, drafted articles, coordinated with freelancers, and edited separate biannual newsletters for Sunnyview Rehabilitation Hospital Foundation and the Northeast Health Foundation. In many instances newsletter articles were repurposed for press releases, news feeds, and social media posts. </p>

                            <ul>
                                <li><a href={withPrefix("/Website_Samples/SunnyviewFoundationNewsletter.pdf")} target="_SVRnewsletter" >Rehabilitation Insight newsletter</a></li>
                                <li><a href={withPrefix("/Website_Samples/NortheastHealthFoundationNewsletter.pdf")} target="_SVRnewsletter" >Northeast Health Foundation newsletter</a></li>
                            </ul>

                        </Details>
                        <Thumbnail>

                            <a href={withPrefix("/Website_Samples/SunnyviewFoundationNewsletter.pdf")} target="_brochure">
                                <img style={imgSty, { border: "solid black" }} src={FoundationsNewsletter} alt="Drop in image" />
                            </a>

                        </Thumbnail>
                    </Content>
                    <hr />
                </Card>

                {/* Walls of History */}
                <Card theme={theme}>
                    <Content>
                        <Details>

                            <h3>Walls of History</h3>
                            <p>Working with the Rensselaer County Historical Society, I led the communication’s team in the creation of historical timelines depicting the evolution of health care in Rensselaer County and the history of Samaritan Hospital School of Nursing. The timelines are displayed in the corridor connecting the Samaritan Hospital Emergency Department and the main lobby. The timelines served as a focal point for the foundation events celebrating the completion of St. Peter’s Health Partners (SPHP) Troy Master Facility Plan project.</p>

                            <ul>

                                <li><a href={withPrefix("/Website_Samples/SamaritanHospitalandSamaritanHosSONmurals/TroyHospitalstimelinemural.pdf")} target="_SHWallofhist" >Samaritan Hospital’s Wall of History</a></li>
                                <li><a href={withPrefix("/Website_Samples/SamaritanHospitalandSamaritanHosSONmurals/TroySONtimelinemural.pdf")} target="_NursingWallofhist">Samaritan Hospital School of Nursing Wall of History</a></li>
                            </ul>

                        </Details>
                        <Thumbnail>

                            <a href={withPrefix("/Website_Samples/SamaritanHospitalandSamaritanHosSONmurals/TroyHospitalstimelinemural.pdf")} target="_brochure">
                                <img style={imgSty, { border: "solid black" }} src={WOH} alt="Drop in image" />
                            </a>

                        </Thumbnail>
                    </Content>
                    <hr />
                </Card>

                {/* Join Our Team (provider recruitment booklet) */}
                <Card theme={theme}>
                    <Content>
                        <Details>

                            <h3>Join Our Team (provider recruitment booklet)</h3>
                            <p>The provider recruitment booklet was created to offer an overview of SPHP, its provider network, and life in the Capital Region. I coordinated the layout of the piece and drafted and edited the content. I worked with the Capital Region Chamber of Commerce which provided the copy for the About the Capital Region section.</p>

                            <ul>

                                <li><a href={withPrefix("/Website_Samples/PhysicianRecruitmentBooklet.pdf")} target="_Recruitmentbooklet" >Join Our Team (provider recruitment booklet)</a></li>
                            </ul>

                        </Details>
                        <Thumbnail>

                            <a href={withPrefix("/Website_Samples/PhysicianRecruitmentBooklet.pdf")} target="_Recruitmentbooklet">
                                <img style={imgSty, { border: "solid black" }} src={RecruitmentBook} alt="Drop in image" />
                            </a>

                        </Thumbnail>
                    </Content>
                    <hr />
                </Card>

            </FlexList>
        </Wrapper>
    </>
    )
}