import React, { useContext } from 'react';
import AnchorLink from 'react-anchor-link-smooth-scroll';
import { ThemeContext } from 'providers/ThemeProvider';
import { Container, Button } from 'components/common';
import dev from 'assets/illustrations/skills.svg';
import { Wrapper, SkillsWrapper, Details, Thumbnail } from './styles';

import tallPam from 'assets/images/pamvertical2_cut_round-square.png';
import resume from 'assets/documents/ResumeOct2020.pdf';

export const Skills = () => {
  const { theme } = useContext(ThemeContext);

  return (
    <Wrapper id="about">
      <SkillsWrapper as={Container}>
        <Thumbnail>
          <img style={{objectFit: "contain"}} src={tallPam} alt="I’m Pam and I’m a Marketer!" />
        </Thumbnail>
        <Details theme={theme}>
          <h1>Meet Pam.</h1>
          <p>
          A self-proclaimed marketing geek, I have a wealth of experience in marketing and communications. I am skilled at collaborating with an organization to develop and implement targeted, impactful, and measurable marketing strategies. 
          </p>
          <p>
          I am a compassionate, highly experienced and innovative marketing professional with excellent writing, communication, and analytical skills. I thrive in an environment where I am able to make a difference in my community and in the lives of others. I am expert in strategy development, public relations, collateral and advertising creation, media planning, television and video direction, website content creation, and social and digital media strategy.
          </p>
          <p>
          Born and raised in picturesque upstate New York, I have a great appreciation for the outdoors, enjoying free time hiking, kayaking, or cross-country skiing. I am especially grateful when able to spend time with family, including my husband and two grown children.
          </p>
          {/* <Button as={AnchorLink} href="#contact">
            Contact Me
          </Button> */}
          <a href='mailto:pamwelch98@gmail.com'>
          <Button >
            Contact Me
          </Button>
          </a>
          <br/>
          
          <br/>

          <a href={resume} target="_resume">
            <Button>
              View My Resume
            </Button>
          </a>
        </Details>
      </SkillsWrapper>
    </Wrapper>
  );
};
